$md: 960px;

:root {
  --pink: #ff3258;
}

a {
  color: #f0e9f2;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
  color: var(--pink);
}

.center {
  margin: auto;
}

h2 {
  display: inline-block;
  border-bottom: 1px solid var(--pink);
  font-size: 3rem;
}

h3 {
  font-size: 2rem;
}

header {
  font-size: calc(10px + 2vmin);
}

#root {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  min-width: 100%;
}

header {
  display: flex;
  flex-direction: column;
  background-image: url(header.jpg);
  min-height: 100vh;
}

main {
  flex: 1;
  padding: 10px;
}

footer {
  text-align: center;
  padding: 20px;
  margin: 30px 0;
}

section {
  border-bottom: 1px solid rgb(36, 35, 35);
  padding: 40px 30px;
}

.section-container {
  margin: auto;
  @media (min-width: $md) {
    max-width: $md;
  }
}

.contact {
  padding: 140px 0;

  &-description {
    margin-bottom: 30px;
    max-width: 500px;
  }
}

.nav-list {
  list-style-type: none;
  margin: 0 35px;
  padding: 10px 20px;

  li {
    display: inline-block;
    margin: 0 10px;
  }

  &.top {
    float: right;
    li {
      margin: 25px 35px;
    }
  }
}

.work-list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0;
  padding: 0;
}

.work {
  display: flex;
  flex-direction: column-reverse;
  list-style-type: none;
  margin: 30px 0 120px 0;

  @media (min-width: $md) {
    flex-direction: row;

    &-description {
      flex-basis: 340px;
      padding-right: 75px;
    }
  }

  &-languages {
    list-style-type: disc;
    padding-left: 20px;
    margin: 20px 0;

    li {
      margin: 3px 0;
      padding: 0;
    }
  }

  &-links {
    a {
      display: inline-block;
      text-decoration: none;
      border-bottom: 1px solid var(--pink);
      padding: 10px;
      margin: 5px;

      &:hover {
        color: white;
      }

      &.icon {
        border: none;

        &:hover {
          border-bottom: 1px solid var(--pink);
        }
      }
    }
  }

  &-image {
    flex: 1;

    img {
      max-width: 100%;
    }
  }
}

.contact-btn {
  background: var(--pink);
  padding: 10px 20px;
  transition: background-color 0.5s;
  border: 1px solid var(--pink);
  display: inline-block;
}

.contact-btn:hover {
  background: none;
}
